import React, {Component} from "react"; 
import SlimProfileMap from "../slimProfileMap"; 

class SlimProfile extends Component {
    state = {
        SlimProfileMap : [{
            id: 1,
            img: "img", 
            subtitle: "subtitle", 
            text: "text",  
            link1: "link1",  
            link2: "link2" 
        }]
    }

    render(){

        console.log(this.state.SlimProfileMap)

        return(
            <div className = "slimProfile">
                <SlimProfileMap SlimProfileMap = {this.state.SlimProfileMap}/>
            </div>
        )
    }
}

export default SlimProfile;