import React, {Component} from "react"; 
import LeftPanel from "../leftPanel";

class PanelA extends Component {
    render(){
        console.log(this.props.panelA)

        return (
            this.props.panelA.map((panelA) => (
                <LeftPanel 
                key = {panelA.id} panelA = {panelA}/>)  
            ))
    }
}
    

export default PanelA; 
