
import React, { useState } from "react";
import axios from "axios";
import {Redirect} from "react-router-dom";
import {Card, Button} from "react-bootstrap";

function SubmitUserForm(props) {
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");  
  const [county, setCounty] = useState("");  
  const [membershipType, setMembershipType] = useState("");  
  const [error, setError] = useState("");


  const updateFirstName = e => {
    setFirstName(e.target.value);
  }; 
  
  const updateLastName = e => {
    setLastName(e.target.value);
  };  
  
  const updateEmail = e => {
    setEmail(e.target.value);
  };  
  
  const updateUsername = e => {
    setUsername(e.target.value);
  };  
  
  const updatePassword = e => {
    setPassword(e.target.value);
  };  
  
  const updateCounty = e => {
    setCounty(e.target.value);
  };

  const updateMembershipType = e => {
    setMembershipType(e.target.value);
  };

  const submitSignUp = e => {
    e.preventDefault();
    axios
      .post("/signup", {
        firstName: firstName, 
        lastName: lastName,
        email: email,
        username: username,
        password: password, 
        county: county, 
        membershipType: membershipType
      })
      .then(response => {
        console.log(response)
        if (response.status === 200) {
          setError("");
          // handle redirecting
          props.handle(response.data);
        } else {
          setError("Username or Password Incorrect");
        }
      })
      .catch(err => {
        setError("UserName Already in use");
      });
  };

  return (
  <Card style={{width:"40rem"}} className="mx-auto mt-4 border border-success">
 { props.user !== null ? <Redirect to="/"/>: ""  }
<Card.Header className="text-center bg-success"><h1> Register</h1></Card.Header>
<Card.Body className="bg-dark">
  <Card.Text className="text-white">

  <label className="mr-2 ml-4" htmlFor="firstName">First Name:</label> <input id="firstName" type="firstName" value={firstName} onChange={updateFirstName} ></input>
  <label className="mr-2 ml-4" htmlFor="lastName">Last Name:</label> <input id="lastName" type="lastName" value={lastName} onChange={updateLastName} ></input>
  <label className="mr-2 ml-4" htmlFor="email">Email:</label> <input id="email" type="email" value={email} onChange={updateEmail} ></input>
  <label className="mr-2 ml-4" htmlFor="username">Username:</label> <input id="username" value={username} onChange={updateUsername} ></input>
  <label className="mr-2 ml-4" htmlFor="password">Password:</label> <input id="password" type="password" value={password} onChange={updatePassword} ></input>
  <label className="mr-2 ml-4" htmlFor="county">County:</label> <input id="county" type="county" value={county} onChange={updateCounty} ></input>
  <label className="mr-2 ml-4" htmlFor="membershipType">Membership Type:</label> <input id="membershipType" type="membershipType" value={membershipType} onChange={updateMembershipType} ></input>

  {error ? <p className="signUp-error">{error}</p> : ""} 
  <Button className="btn btn-success mt-4 flat btn-block"
   onClick={submitSignUp}>Submit</Button>

  </Card.Text>
</Card.Body>
</Card>
  );
}

export default SubmitUserForm;
