import React from "react"; 
import Hands from './hands.jpg';

const Landing = (props) => {
    return (

        <div className = "landing"> 
            <header className="header">
            <img src={Hands} className="App-logo" alt="logo" />
            <br/>
            <div className = "landingIntro">
                <a
                    id="landingLink"
                    href="https://Helping_Hands.heroku-app.com"
                    target="/signIn"
                    rel="">
                </a>
            </div>
            </header>
        </div>
    )
}

export default Landing;