import React, {Component} from "react"; 
import SlimProfileCard from "../slimProfileCard";

class SlimProfileMap extends Component {
    render(){
        console.log(this.props.SlimProfileMap)

        return (
            this.props.SlimProfileMap.map((SlimProfileMap) => (
                <SlimProfileCard key = {SlimProfileMap.id} SlimProfileMap= {SlimProfileMap}/>)  
            ))
    }
}
    

export default SlimProfileMap; 