import React, {Component} from "react"; 
import FeedMap from "../feedMap"

class Feed extends Component {
    state = {
        FeedMap : [{
            id : 1,
            title : "title", 
            pic: "pic", 
            subtitle: "subtitle", 
            text: "text",  
            link: "link",   
        }]
    }

    render(){
        console.log(this.state.FeedMap)

        return(
            <div className = "feed">
                <FeedMap FeedMap = {this.state.FeedMap}/>
            </div>
        )
    }
}

export default Feed