import React, {Component} from "react"; 
import NewsMap from "../newsMap"

class News extends Component {
    state = {
        newsMap : [{
            id: 1,
            title : "title",  
            subtitle: "subtitle", 
            text: "text",  
            link: "link",   
        }]
    }

    render(){
        console.log(this.state.newsMap)

        return(
            <div className = "feed">
                <NewsMap newsMap = {this.state.newsMap}/>
            </div>
        )
    }
}

export default News