import React, {Component} from "react";
import {Container, Card} from "react-bootstrap";

class SlimProfileCard extends Component {
  
  render(){
    return (
     <div>
       <Container className = "slimProfileContainer">        
        <Card >
          <Card.Body>
            <Card.Title>{this.props.SlimProfileMap.img}</Card.Title>
            <Card.Subtitle className="mb-2 text-muted">{this.props.SlimProfileMap.subtitle}</Card.Subtitle>
            <Card.Text>{this.props.SlimProfileMap.text}
            </Card.Text>
            <Card.Link href="#">{this.props.SlimProfileMap.link1}</Card.Link>
            <Card.Link href="#"> {this.props.SlimProfileMap.link2}</Card.Link>
          </Card.Body>
        </Card>
        </Container>
     </div>   
    )
    }
  }

export default SlimProfileCard;

