import React, {Component} from "react"; 
import PanelA from "../panelA"; 

class Panels extends Component {
    state = {
        panelA : [{
            id: 1,
            title : "title", 
            pic: "pic", 
            subtitle: "subtitle", 
            text: "text",  
            link1: "link1",  
            link2: "link2" 
        }]
    }


    render(){

        console.log(this.state.panelA)

        return(
            <div className = "panels">
                <PanelA panelA = {this.state.panelA}/>
            </div>
        )
    }
}

export default Panels;