import UserHome from './components/appContainer/userHome';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {

    };
  } 
  render(){
    return(
      <Router>
        <Header/>
      <Switch>
        <Route exact path = "/" component = {Landing} />
        <Route exact path = "/userHome" component = {UserHome} />
      </Switch>
        <Footer/>
      </Router>
    )
  }
}

export default App;
