import React, {Component} from "react";
import {Container, Card} from "react-bootstrap";

class NewsCard extends Component {
  
  render(){
    return (
     <div>
       <Container className = "newsCardContainer">        
        <Card >
          <Card.Body>
            <Card.Title>{this.props.newsMap.title}</Card.Title>
            <Card.Subtitle className="mb-2 text-muted">{this.props.newsMap.subtitle}</Card.Subtitle>
            <Card.Text>{this.props.newsMap.text}
            </Card.Text>
            <Card.Link href="#">{this.props.newsMap.link}</Card.Link>
          </Card.Body>
        </Card>
        </Container>
     </div>   
    )
    }
  }

export default NewsCard;


