import React, { Component } from "react";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import axios from "axios";
import LaunchLogin from "../launchLogin";
import UserForm from "../submitUserForm";

class Header extends Component{
    constructor(props){
      super(props)
      this.state={
        username: "",
        showSignUp : false

      }
    }
  
   componentDidMount(){
     axios.get("/loggedin")
     .then(res=> 
      this.setState({
        user: res.data.username
      }))
   }

    logOut = () => {
        axios.post('/logout')
    }

    signUp = () => {
    }
 


  render(){

    return (
      <Navbar style={{background:"orange"}}>

        <h2 style={{color:"white"}} className="ml-3 mt-2">Helping Hands</h2>
          <Navbar.Brand href="#landing"></Navbar.Brand>
            <Nav className="ml-auto font-weight-bold">
        
               
   
                {this.props.user !== null ?   
                    <Nav>
                    <Nav.Link>
                        <UserForm href = "/signup"/>          
                    </Nav.Link>

                    <Nav.Link>
                        <LaunchLogin href = "/logIn"/>           
                    </Nav.Link>      
                    </Nav>               
                    :
                    <Nav>
                        <Nav.Link style={{color:"white"}}> 
                        Welcome {this.props.user}
                        </Nav.Link>

                        <Nav.Link style={{color:"white"}}> 
                            Account Details
                        </Nav.Link>  

                        <Nav.Link onClick={this.logOut} style={{color:"white"}} href="/logOut">Logout</Nav.Link> 
                    </Nav>     
                }
                </Nav>
          </Navbar>
          )
  }
}
    
export default Header;