import React, {Component} from "react";
import {Container, Card} from "react-bootstrap";

class FeedCard extends Component {
  
  render(){
    return (
     <div>
       <Container className = "leftPanelContainer">        
        <Card >
          <Card.Body>
            <Card.Title>{this.props.FeedMap.title}</Card.Title>
            <Card.Subtitle className="mb-2 text-muted">{this.props.FeedMap.subtitle}</Card.Subtitle>
            <Card.Text>{this.props.FeedMap.text}
            </Card.Text>
            <Card.Link href="#">{this.props.FeedMap.link}</Card.Link>
          </Card.Body>
        </Card>
        </Container>
     </div>   
    )
    }
  }

export default FeedCard;


