import React, {Component} from "react";
import {Container, Card} from "react-bootstrap";
import "./leftPanel.css"

class LeftPanel extends Component {
  
  render(){
    return (
     <div>
       <Container className = "leftPanelContainer">        
        <Card >
          <Card.Body>
            <Card.Title>{this.props.panelA.title}</Card.Title>
            <Card.Subtitle className="mb-2 text-muted">{this.props.panelA.subtitle}</Card.Subtitle>
            <Card.Text>{this.props.panelA.text}
            </Card.Text>
            <Card.Link href="#">{this.props.panelA.link1}</Card.Link>
            <Card.Link href="#"> {this.props.panelA.link2}</Card.Link>
          </Card.Body>
        </Card>
        </Container>
     </div>   
    )
    }
  }

export default LeftPanel;


