import React from "react";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import Dropdown from 'react-bootstrap/Dropdown'
import NavDropdown from 'react-bootstrap/NavDropdown'
import Main from "../aboutMain/"
import axios from "axios";

class Footer extends React.Component {
    constructor(props){
      super(props)
      this.state={
        username: ""
      }
    }
  
   componentDidMount(){
     axios.get("/loggedin")
     .then(res=> 
      this.setState({
        username: res.data.username
      }))
   }

   logOut = () => {
    axios.post('/logout')
  }

  render(){
    return (
      <Navbar style={{background:"orange"}}>

        <h5 style={{color:"white"}} className="ml-3 mt-2">Copyright 2020 | Nyleesia Nicome Schnarr</h5>
          <Navbar.Brand href="#landing"></Navbar.Brand>
            <Nav className="ml-auto font-weight-bold">
        


        {this.props.user !== null ? 
            <NavDropdown style={{color:"white"}} href="contact" title ="Contact Us" > 
            <Dropdown.Item eventKey="1" href = "/credo">We Care 
           </Dropdown.Item>
            <Dropdown.Item eventKey="2">Send a Message</Dropdown.Item>
            </NavDropdown>
        :""
        }

        {this.props.user !== null ?         <NavDropdown style={{color:"white"}} href="more" title = "More Info" >
            <Dropdown.Item eventKey="1">Employment at Helping Hands</Dropdown.Item>
            <Dropdown.Item eventKey="2">Site Map</Dropdown.Item>
            </NavDropdown> 
       : ""
       }
            </Nav>
          </Navbar>
          )
  }
    
  }
  
export default Footer;